<template>
  <div class='counseling'>
    <div class='header'>
      
    </div>
    <el-row>
      <el-col :span='10'>
        <div class='card-info student-info'>
          <el-avatar :size='100' :src='student.avatar'></el-avatar>
          <div class='name'>{{ student.name }}</div>
          <div class='class'>{{ student.class }}</div>
        </div>
      </el-col>
      <el-col :span='6' :offset='2'>
        <div class='card-info' @click='onAsk'>
          <el-avatar :size='100' :src='student.avatar'></el-avatar>
          <div class='title'></div>
        </div>
      </el-col>
      <el-col :span='6'>
        <div class='card-info' @click='goList'>
          <el-avatar :size='100' :src='student.avatar'></el-avatar>
          <div class='title'></div>
        </div>
      </el-col>

    </el-row>

    <el-dialog
      title=''
      :visible.sync='showAskDialog'
      width='30%'
      center
      @close='cancelRecord'>
      <div class='counseling-content'>
        <!--      -->
        <div>
        <audio :src='audio' controls></audio>
        </div>
        <!--   -->
        <span class='duration'>{{ recorder_params.duration }}s</span>
      </div>
      <span slot='footer' class='dialog-footer'>
       <el-button type='primary' @click='startRecord' v-if='recorder_status===0'></el-button>
       <el-button type='primary' @click='pauseRecord' v-if='recorder_status===1||recorder_status===3'></el-button>
       <el-button type='primary' @click='resumeRecord' v-if='recorder_status===2'></el-button>
       <el-button @click='stopRecord' v-if='recorder_status!==4'></el-button>
        <template v-if='recorder_status===4'>
        <el-button type='primary' @click='submitRecord'></el-button>
        <el-button @click='cancelRecord'></el-button>
        </template>
      </span>
    </el-dialog>

    <canvas id='canvas'></canvas>
  </div>
</template>

<script>
const lamejs = require('lamejstmp');
export default {
  name: 'counseling',
  data() {
    return {
      student: {
        avatar: '',
        name: '',
        class: ''
      },
      showAskDialog: true,
      recorder: null,
      recorder_params: {
        duration: 0
      },
      recorder_status: 0, //1  2   3   4 
      //
      drawRecordId: null,
      oCanvas: null,
      ctx: null,
      audio: '',
      chunkList: {
        duration:0,
        stream: null
      },
      chunks:[]
    };
  },
  mounted() {

    // this.recorder.onprogress = (params) => {
    //   console.log('()', params.duration);
    //   console.log('()', params.fileSize);
    //   console.log('(%)', params.vol);
    //   // console.log('([DataView, DataView...])', params.data);
    //   this.recorder_params.duration = params.duration.toFixed(2);
    // };
    this.startCanvas();
    // this.audio = this.$refs.audio
  },
  methods: {
    //
    startCanvas() {

    },

    onPlay (index) {
      this.$set(this.chunkList, 'wink', false);

      this.audio.src = this.chunkList.stream;
      this.audio.play();

      // this.bindAudioEvent(index);
    },

    bindAudioEvent (index) {
      let item = this.chunkList;

      this.audio.onplaying = () => {
        this.$set(item, 'wink', true);
      }

      this.audio.onended = () => {
        this.$set(item, 'wink', false);
      }
    },
    bindEvents () {
      this.recorder.ondataavailable = this.getRecordingData;
      this.recorder.onstop = this.saveRecordingData;
    },

    getRecordingData (e) {
      this.chunks.push(e.data);
    },

    saveRecordingData  () {
      let blob = new Blob(this.chunks, { 'type' : 'audio/wav' }),
        audioStream = URL.createObjectURL(blob),
        //
        duration = parseInt(blob.size / 6600);
      console.log(duration,'duration');
      if(duration <= 0) {
        alert('');
        return;
      }
      // if(duration > 60) {
      //   duration = 60;
      // }
      this.chunkList.duration = duration
      this.chunkList.stream = audioStream
      this.audio = audioStream
      this.chunks = [];
    },
    requestAudioAccess () {
      navigator.mediaDevices.getUserMedia({audio: true}).then(stream => {
        this.recorder = new window.MediaRecorder(stream);
        this.bindEvents();
        this.recorder.start()
        this.recorder_status = 1;
      }, error => {
        alert('');
      });
    },
    //
    onAsk() {
      this.showAskDialog = true;
    },
    //
    startRecord() {
      if (!navigator.mediaDevices) {
        alert('');
        return;
      }
      if (!window.MediaRecorder) {
        alert('');
        return;
      }
      this.requestAudioAccess();


    },
    //
    pauseRecord() {
      this.recorder.pause();
      this.recorder_status = 2;
    },
    //
    resumeRecord() {
      this.recorder.resume();
      this.recorder_status = 3;
    },
    //
    stopRecord() {

      this.recorder.stop();
      this.recorder_status = 4;
      // this.onPlay(0)
      return;
      window.URL = window.URL || window.webkitURL;
      //
      const mp3Blob = this.convertToMp3(this.recorder.getWAV());
      console.log(mp3Blob);
      this.audio = window.URL.createObjectURL(this.recorder.getWAVBlob()) ;
    },
    //
    cancelRecord() {
      this.showAskDialog = false;
      this.recorder_status = 0;
      if (this.recorder_params.duration) {
        this.recorder.initRecorder();
        this.recorder_params.duration =0
        this.audio=''
      }
    },
    //
    submitRecord() {
      // this.cancelRecord();
      //todo:

    },
    //
    goList(){
      this.$router.push('/psychologicalCounselingList')
    },
    //wavMP3
    convertToMp3(wavDataView) {
      // wav
      const wav = lamejs.WavHeader.readHeader(wavDataView); // wavconfig
      console.log(wav,'wav')
      const { channels, sampleRate } = wav;
      const mp3enc = new lamejs.Mp3Encoder(channels, sampleRate, 128);
      // 
      const result = this.recorder.getChannelData();
      const buffer = [];

      const leftData = result.left && new Int16Array(result.left.buffer, 0, result.left.byteLength / 2);
      const rightData = result.right && new Int16Array(result.right.buffer, 0, result.right.byteLength / 2);
      const remaining = leftData.length + (rightData ? rightData.length : 0);

      const maxSamples = 1152;
      for (let i = 0; i < remaining; i += maxSamples) {
        const left = leftData.subarray(i, i + maxSamples);
        let right = null;
        let mp3buf = null;

        if (channels === 2) {
          right = rightData.subarray(i, i + maxSamples);
          mp3buf = mp3enc.encodeBuffer(left, right);
        } else {
          mp3buf = mp3enc.encodeBuffer(left);
        }

        if (mp3buf.length > 0) {
          buffer.push(mp3buf);
        }
      }

      const enc = mp3enc.flush();

      if (enc.length > 0) {
        buffer.push(enc);
      }

      return new Blob(buffer, { type: 'audio/mp3' });
    },


  }
};
</script>

<style scoped lang='scss'>
.counseling {
  height: 100%;
}

.header {
  text-align: center;
  color: #fff;
  background: #2a33e6;
  width: 100%;
  padding: 10px 0;
}

.card-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 200px;
}

.student-info {
  border-right: 1px solid #eee
}

.title {
  font-weight: bold;
  font-size: 18px;
}

.el-row, .el-col {
  height: 100%;
}

.counseling-content {
  text-align: center;
  padding: 10px 0;
}
.duration{
  margin-top: 10px;
}
</style>



